@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
/* base styles */
html, body, #root {
  height: 100%;
}
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.4em;
  background: #dfdfdf;
  color: #333;
}
h1,h2,h3,p {
  margin: 0;
}
input, textarea {
  color: #333;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}
.page-title {
  text-align: center;
  margin: 40px auto;
  color: #333;
}
.loading, .error {
  text-align: center;
  margin: 40px auto;
}
.App {
  min-height: 100%;
}
.navbar {
    background-color: rgb(31, 30, 30);
    padding: 20px;
    color:aqua;
}
.navbar nav{
    display: flex;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
}

.navbar a.brand {
    margin-right: auto;
    color:rgb(30, 102, 165);
    text-decoration: none;
}

.navbar a:last-child{
    color:rgb(255, 255, 255);
    text-decoration: none;
    margin-left: 40px;
    padding: 8px;
    border-radius: 5px;
    border: 1px solid rgb(229, 227, 227);
}
.navbar a:last-child:hover{
    background-color: rgb(247, 247, 247);
    color:rgb(1, 7, 13);
}
.home {
    max-width: 1200px;
    margin: 60px auto;
}

.place-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  max-width: 1200px;
  margin: 40px auto;
}
.place-list .card {
  background-color: white;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  position: relative;
  padding: 20px;
}
.place-list .card:hover {
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}
/* .place-list .card .card-img-top {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 4px 4px 0 0;
} */
.place-list .card h3 {
  color: #555;
  margin-bottom: 6px;
}
.place-list .card p {
  color: #999;
  font-size: 0.9em;
}
.place-list .card div{
  color: #555;
  font-size: 0.7em;
  margin: 20px 0;
  line-height: 1.5em;
}
.place-list .card a {
    color: rgb(255, 255, 255);
    text-decoration: none;
    display:block;
    background: #324f63;
    font-size: 0.9em;
    width: 100px;
    text-align: center;
    padding: 5px;
    border-radius: 4px;
    margin: 20px auto 0;
}



